<template>
    <div class="container">
        <div class="content">
            <iframe :src="url" frameborder="0" class="iframe"></iframe>
        </div>
        <div class="close" @click="this.close">×</div>
    </div>
</template>

<style lang="scss" scoped>
    .container,.content{
        height: 80vh;
        width: 80vw;
        background: #fff;
        position: fixed;
        top: 10vh;
        left: 10vw;
        border-radius: 5px;
        z-index: 100;
    }
    .content{
        overflow: hidden;
        box-shadow: 0 0 10px #000;
    }
    .iframe{
        height: 100%;
        width: 100%;
    }
    .close{
        position: absolute;
        right: -15px;
        top: -15px;
        z-index: 101;
        background: #fff;
        border-radius: 50%;
        box-shadow: 0 0 10px #000;
        font-size: 30px;
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 30px;
    }
</style>

<script>
export default {
    data(){
        return {
            html: ''
        }
    },
    props:['data'],
    methods:{
        close(){
            this.$emit('close');
        },
        http_builder_url(url, data) {
            if(typeof(url) == 'undefined' || url == null || url == '') {
                return '';
            }
            if(typeof(data) == 'undefined' || data == null || typeof(data) != 'object') {
                return '';
            }
            url += (url.indexOf("?") != -1) ? "" : "?";
            for(var k in data) {
                url += ((url.indexOf("=") != -1) ? "&" : "") + k + "=" + encodeURI(data[k]);
                console.log(url);
            }
            return url;
        }
    },
    computed:{
        url(){
            return this.http_builder_url(this.data.loadUrl,this.data.para);
        }
    },
    mounted(){
        let _this = this;
        window.closeiFrame = function(){
            _this.close();
        }
    }
}
</script>




