export default {
    state: {
        loading: false,
        alert: {
            text: null,
            time: 2000,
        },
        html_container_url: null,
    },
    mutations: {
        loading(state,status){
            state.loading = status;
        },
        alert(state,data){
            state.alert.text = data.text;
            state.alert.time = data.time ? data.time : 2000;
        },
        loadUrl(state,data){
            state.html_container_url = data;
        },
        unloadUrl(state){
            state.html_container_url = null;
        }

    }
}


